@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  margin-left: 30px;
  margin-right: 30px;
}
@media (min-width: 768px) {
  .container {
    margin-left: 6.5%;
    margin-right: 6.5%;
  }
}
@media (min-width: 1150px) {
  .container {
    margin-left: 4.36%;
    margin-right: 4.36%;
  }
  .container--about-us {
    margin-left: 16.7%;
    margin-right: 16.7%;
  }
  .container--contacts {
    grid-column: 3/4;
    grid-row: 4/5;
    margin: 0;
    width: 79%;
  }
  .container--form {
    margin-left: 112px;
    margin-right: 226px;
  }
  .container--form-color {
    margin-left: 112px;
    margin-right: 172px;
  }
  .container--form-contacts {
    margin-left: 112px;
    margin-right: 226px;
  }
}

.link {
  outline: 1px solid transparent;
}
.link:hover {
  opacity: 0.6;
}
.link:focus {
  outline: 2px solid #ff8104;
  position: relative;
  z-index: 1;
}
.link:active {
  opacity: 0.3;
}

.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:focus {
  outline: 2px solid #ff8104;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}

.page {
  height: 100%;
  font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #444444;
}
@media (min-width: 1150px) {
  .page__body {
    max-width: 1150px;
    margin: 0 auto;
  }
}

.page-header {
  background-color: #ffffff;
  position: absolute;
  z-index: 1;
  width: 100%;
}
@media (min-width: 768px) {
  .page-header {
    margin: 0;
    position: static;
  }
}
@media (min-width: 1150px) {
  .page-header {
    margin: 0;
  }
}

.page-nav {
  position: relative;
}
@media (min-width: 768px) {
  .page-nav {
    min-height: 154px;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
  }
}
@media (min-width: 1150px) {
  .page-nav {
    min-height: 0;
  }
}

.page-nav__toggle {
  position: absolute;
  right: 23px;
  top: -1px;
  padding: 17px;
  margin: 0;
}

.page-nav--closed .page-nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  right: 7px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}

@media (min-width: 320px) {
  .page-nav--closed .page-nav__catalog,
.page-nav--closed .page-nav__order,
.page-nav--closed .search,
.page-nav--closed .basket {
    display: none;
  }

  .page-nav--nojs .page-nav__toggle {
    display: none;
  }

  .page-nav--nojs .page-nav {
    position: static;
  }

  .page-nav--nojs .page-nav__catalog,
.page-nav--nojs .page-nav__order,
.page-nav--nojs .search,
.page-nav--nojs .basket {
    display: block;
  }
}
.page-nav--opened .page-nav__toggle::before, .page-nav--opened .page-nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  left: 4px;
}
.page-nav--opened .page-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-nav--opened .page-nav__toggle::after {
  transform: rotate(-45deg);
}

@media (min-width: 768px) {
  .page-nav--closed .page-nav__catalog,
.page-nav--closed .page-nav__order,
.page-nav--closed .search,
.page-nav--closed .basket {
    display: flex;
  }
}
.page-nav__catalog {
  position: relative;
  display: block;
}
.page-nav__catalog::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #ececec;
  top: 0;
}
@media (min-width: 768px) {
  .page-nav__catalog {
    padding: 0 36px 0 50px;
    align-items: center;
    order: 3;
    width: 28.4%;
    box-sizing: border-box;
    background-color: #f9f9f9;
  }
  .page-nav__catalog::before {
    width: 0;
    height: 0;
  }
  .page-nav__catalog--catalog {
    background-color: #f1f1f1;
  }
}
@media (min-width: 1150px) {
  .page-nav__catalog {
    order: 0;
    width: 20.4%;
    background-color: transparent;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.page-nav__order {
  display: block;
  position: relative;
}
.page-nav__order::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #ececec;
  top: 0;
}
@media (min-width: 768px) {
  .page-nav__order {
    padding: 0 15px 0 0;
    align-items: center;
    order: 4;
    width: 35%;
    box-sizing: border-box;
    background-color: #f9f9f9;
  }
  .page-nav__order::before {
    width: 0;
    height: 0;
  }
  .page-nav__order--catalog {
    background-color: #f1f1f1;
  }
}
@media (min-width: 1150px) {
  .page-nav__order {
    order: 1;
    width: 22%;
    padding-left: 34px;
    background-color: transparent;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.page-nav__catalog-link,
.page-nav__order-link {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  font-weight: 700;
  font-size: 17px;
  color: #000000;
  display: block;
  padding: 27.5px 76px 27.5px;
}
@media (min-width: 768px) {
  .page-nav__catalog-link,
.page-nav__order-link {
    font-size: 15px;
    padding: 0;
  }
}
@media (min-width: 1150px) {
  .page-nav__catalog-link,
.page-nav__order-link {
    font-size: 17px;
  }
}

.nav-toggle {
  border: none;
  background-color: transparent;
  width: 20px;
  height: 14px;
  cursor: pointer;
  outline: 1px solid transparent;
}
@media (min-width: 768px) {
  .nav-toggle {
    display: none;
  }
}
.nav-toggle:focus {
  outline: 2px solid #ff8104;
}

.logo {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .logo {
    display: flex;
    align-items: center;
    order: 0;
    min-width: 48.3%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 21px 0 21px 50px;
    box-sizing: border-box;
  }
}
@media (min-width: 1150px) {
  .logo {
    order: 2;
    min-width: 22.9%;
    padding: 8px 12px;
  }
}
.logo:hover .logo-image {
  opacity: 0.6;
}
.logo:focus .logo-image {
  outline: 2px solid #ff8104;
}
.logo:active .logo-image {
  opacity: 0.3;
}

.logo__image {
  margin: 22px auto;
}
@media (min-width: 768px) {
  .logo__image {
    margin: 0;
  }
}

.logo-image {
  display: block;
  outline: 1px solid transparent;
}
.logo-image--index {
  cursor: auto;
}

.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 17px;
  color: #000000;
}
@media (min-width: 768px) {
  .nav-list {
    display: flex;
    align-items: center;
    order: 5;
    flex-wrap: wrap;
    width: 36.6%;
    box-sizing: border-box;
    background-color: #f9f9f9;
  }
  .nav-list--catalog {
    background-color: #f1f1f1;
  }
}
@media (min-width: 1150px) {
  .nav-list {
    width: 100%;
  }
}

.nav-list-item--delivery, .nav-list-item--new, .nav-list-item--sale {
  display: none;
}
@media (min-width: 768px) {
  .nav-list-item {
    padding: 22px 0 20px;
  }
  .nav-list-item--delivery {
    display: block;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #444444;
    padding-right: 50px;
  }
}
@media (min-width: 1150px) {
  .nav-list-item {
    display: block;
    padding: 19px 0 20px;
    box-sizing: border-box;
  }
  .nav-list-item--new {
    padding-left: 50px;
    width: 235px;
  }
  .nav-list-item--sale {
    margin-right: auto;
    width: 50%;
    padding-left: 35px;
  }
  .nav-list-item--delivery {
    text-align: right;
    padding-right: 50px;
    width: 309px;
  }
}

.nav-list-item-link {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  color: inherit;
  font-size: 15px;
}

.search {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  padding: 28.5px 0 28.5px 0;
  font-size: 17px;
  display: flex;
  position: relative;
  outline: 1px solid transparent;
}
.search::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #ececec;
  top: 0;
}
@media (min-width: 320px) {
  .search:focus {
    outline: 2px solid #ff8104;
  }
}
@media (min-width: 768px) {
  .search {
    min-width: 10.7%;
    order: 1;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-top: none;
    box-sizing: border-box;
  }
  .search::before {
    width: 0;
    height: 0;
  }
  .search:focus {
    outline: 1px solid transparent;
  }
  .search:focus .search__wrapper {
    outline: 2px solid #ff8104;
  }
}
@media (min-width: 1150px) {
  .search {
    order: 3;
    min-width: 7.2%;
  }
}

.search__wrapper {
  margin-left: 30px;
  margin-right: 30px;
}
@media (min-width: 768px) {
  .search__wrapper {
    margin: auto;
  }
}

.wrapper {
  display: flex;
  outline: 1px solid transparent;
}
.wrapper:hover .wrapper-icon {
  fill: #63d1bb;
  opacity: 1;
}
.wrapper:hover .wrapper-title {
  opacity: 0.6;
}
.wrapper:active .wrapper-icon {
  fill: #63d1bb;
  opacity: 0.3;
}
.wrapper:active .wrapper-title {
  opacity: 0.3;
}

.wrapper-icon {
  align-self: center;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .wrapper-icon--basket {
    margin-right: 32px;
  }
}
@media (min-width: 1150px) {
  .wrapper-icon {
    grid-column: 2/3;
  }
}

.wrapper-title {
  font-weight: 700;
  font-size: 17px;
  color: #000000;
  font-size: inherit;
  padding-left: 24px;
}
.wrapper-title--basket {
  padding-left: 21px;
}
@media (min-width: 768px) {
  .wrapper-title {
    display: none;
  }
  .wrapper-title--basket {
    font-weight: 700;
    font-size: 17px;
