    padding-left: 0;
    display: inline-block;
  }
}

.basket {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  padding: 27.5px 0 27.5px 0;
  font-size: 17px;
  display: flex;
  position: relative;
  outline: 1px solid transparent;
}
.basket::before, .basket::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #ececec;
}
.basket::before {
  top: 0;
}
.basket::after {
  bottom: 0;
}
@media (min-width: 320px) {
  .basket:focus {
    outline: 2px solid #ff8104;
  }
}
@media (min-width: 768px) {
  .basket {
    align-items: center;
    flex-wrap: wrap;
    order: 2;
    width: 41%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    cursor: pointer;
    box-sizing: border-box;
    padding: 0;
  }
  .basket::before, .basket::after {
    width: 0;
    height: 0;
  }
  .basket:focus {
    outline: 1px solid transparent;
  }
  .basket:focus .basket__wrapper {
    outline: 2px solid #ff8104;
  }
}
@media (min-width: 1150px) {
